.error-page{
    margin: 0 auto;
}
.error-page .d-flex{
    height: 100vh;
    position: relative;
    z-index: 9;
}
.error-page .backdrop-text{
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.error-page i{
    font-size: 70vw;
    color: rgba(0, 123,255,.1);
}
.error-page h1{
    color: rgba(0, 123,255,1);
    font-size: 10rem;
}