.row .mb-3 + .legend-container .legend-group{
  margin-top: 0 !important;
}
.legend-group {
  border-bottom: 3px solid var(--mustard-color);
}
.legend-group .fa-user-plus, .legend-group .fa-trash-restore-alt {
  cursor: pointer;
}
.add-staff.dropdown{
  position: absolute;
  left: 50%; 
  right: 0;
  display: none;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  background: var(--white-color);
  transform: translateX(-50%);
  max-width: 80%;
  z-index: 99;
}


/* attachments */
.thumbnail{
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px, rgba(0, 0, 0, 0.1) 0px 0px 1px 0px;
  border-radius: 8px;
}

.docu-attachment{
  position: relative;
}
.attachment-option{
  display: none;
}
.attachment-option .d-flex{
  height: 100%;
}
.attachment-option .d-flex label, 
.attachment-option .d-flex a{
  cursor: pointer;
  margin: .3em;
}

.attachment-option a{
  color: var(--white-color);
  text-decoration: none;
  display: block;
}
.attachment-option::after{
  content: "";
  position: absolute;
  background: var(--turquoise-color);
  opacity: .7;
  display: block;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
.docu-attachment:hover .attachment-option{
  color: var(--white-color);
  font-size: 4rem;
  display: block;
  position: absolute;
  width: 100%;
  top:  0;
  bottom: 0;
  flex: 1;
  z-index: 1;
}

@keyframes FadeIn { 
  from {transform: translateX(-250px);}
  to {transform: translateX(0)}
  /* 0% {
    opacity: 0;
    /* transform: scale(.1); *
  }

  85% {
    opacity: .5;
    /* transform: scale(1.05); *
  }
  100% {
    opacity: 1;
    /* transform: scale(1); *
  } */
}

#team-container0.active{
  animation: FadeIn 1s linear;
  animation-fill-mode: both;
}


/* fade in right */
@keyframes FadeInRight { 
  from {transform: translateX(250px);}
  to {transform: translateX(0)}
}

#fadeInRight{
  transform: translateX(250px);
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
}
#fadeInRight.active{
  animation: FadeInRight 1s ease-in-out;
  animation-fill-mode: both;
}